<template>
  <div>
    <br class="fat-only"/>

    <div class="ui segment container">
      <h1 class="ui header">如何新增照片：</h1>
      <div class="ui large ordered divided list">
        <div class="item">請將您自選的照片上傳至<a href = "https://www.imgur.com" target="_blank">imgur網站</a></div>
        <div class="item">在<a href = "https://www.imgur.com" target="_blank">imgur網站</a>上取得該圖片網址</div>
        <div class="item">將該圖片網址貼上「新增圖片網址」欄位<br/>
        <input type ="text" v-model="url" placeholder="新增圖片網址"></div>
        <div class="item">在「輸入名字」欄位上輸入人名<br/><input type ="text" v-model="name" placeholder="輸入名字"></div>
        <div class="item">再按「新增」即可<a class = "ui green button" @click="add(url, name); url = ''; name = ''"><i class="plus icon"/>新增{{name}}</a></div>
      </div>
      <div class="ui action input fat-only">
      </div>
    </div><!-- Segment END -->
    <br/>
    <div class="ui four doubling cards">
      <div class="ui card" v-show="url || name">
        <div class="content">
          <div class = "image">
            <img :src="url"/>
          </div>
        </div>
        <div class="extra content">
          <a class="ui green basic button" @click="add(url, name); url = ''; name = ''" title="新增"><i class="plus square icon" />新增{{name}}</a>
        </div>
      </div>
      <div class="ui card" v-for = "(c, index) in card_list" :key="index">
        <div class="content">
          <a class = "image" @click="url = c.img; name = c.name">
            <img :src="c.img"/>
          </a>
        </div>
        <div class="extra content">
          <a class="ui red basic button" @click="remove(index)" title="刪除"><i class="window close icon" />{{c.name}}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Editor',
  props: ['card_list'],
  data () {
    return {
      url: '',
      name: ''
    }
  },
  methods: {
    add: function (url, name) {
      this.$emit('add', url, name)
    },
    remove: function (index) {
      this.$emit('remove', index)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  img {
    height: 15vmin;
    max-width: 100%;
    border: 3px gold ridge;
  }
  a {
    cursor: pointer;
  }
  a:hover {
    text-decoration: underline;
  }
  input {
    max-width: 75vw !important;
  }
  .fluid {
    width: 100%;
    max-width: 100% !important;
  }
  .list .item {
    font-size: 1.6em;
  }
</style>



